.stars {
  display: flex;

  @for $index from 1 through 5 {
    &--#{$index} :nth-child(-n + #{$index}) {
      background-image: url(../images/star-active.svg);
    }
  }

  &__star {
    width: 16px;
    height: 16px;
    margin-right: 4px;
    border-radius: 0.5px;
    background-image: url(../images/star.svg);
    background-repeat: no-repeat;
    background-position: center;
  }
}
